import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { CssBaseline, Container } from '@material-ui/core';
import Header from './components/Header';
import Footer from './components/Footer';
import MPCert from './views/MPCert';
import MPAM from './views/MPAM';
import IAP from './views/IAP';
import Protools from './views/Protools';
import Quiz from './views/Quiz';
import WW from './views/WW';
import Orientation from './views/Orientation';
import Prepclass from './views/Prepclass';
import Physics from './views/Physics';
import MIDI from './views/Midi';
import MusicBusiness from './views/MusicBusiness';
import Resume from './views/Resume';
import Biography from './views/Biography';
import Landing from './views/Landing';


const App = () => {
  return (
    <div>
        <CssBaseline />
        <Header />
          <main>
            <Switch>
              <Route exact path="/" component={Landing} />
              <Container disableGutters>
                <Route path="/mpcert">
                  <MPCert />
                </Route>
                <Route path="/mpam">
                  <MPAM />
                </Route>
                <Route path="/introtoaudio" component={IAP} />
                <Route path="/protools" component={Protools} />
                <Route path="/onlinequiz" component={Quiz} />
                <Route path="/whatchuwatchin" component={WW} />
                <Route path="/orientation" component={Orientation} />
                <Route path="/prepclass" component={Prepclass} />
                <Route path="/physicsofsound" component={Physics} />
                <Route path="/midisynth" component={MIDI} />
                <Route path="/musicbusiness" component={MusicBusiness} />
                <Route path="/resume" component={Resume} />
                <Route path="/biography" component={Biography} />
              </Container>
            </Switch>
          </main>
        <Footer/>
      </div>
  );
}

export default App;